.title {
  font-size: 5vw;
  text-transform: uppercase;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif,
    "montserrat";
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  height: 35vh;
  width: 100%;
}

@media (max-width: 900px) {
  .header {
    justify-content: space-evenly;
    height: 25vh;
  }

  .title {
    font-size: 8vw;
  }
}

.inputs {
  width: 100%;
  justify-content: space-around;
  display: flex;
}
.search,
.select {
  width: 43%;
}
