.meanings {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  height: 55vh;
  border: 10px solid rgb(165, 159, 255);
  border-radius: 10px;
  padding: 10px 20px;
  overflow-x: hidden;
  border-style: dotted;
}

.intro {
  font-size: 5vw;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif,
    "montserrat";
}

@media (max-width: 900px) {
  .meanings {
    height: 60vh;
    overflow-x: hidden;
    overflow-y: scroll;
  }
}

.singleMean {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding: 10px 20px;
  margin: 10px 0px;
}
